import React, {Component} from 'react';
import { Link } from "react-router-dom"; //SPA 방식 활용

class RouteTest extends Component {
    render() {
        return (
            <div>
                <Link to={'/'}>Home</Link>
                <br/>
                <Link to={'/new'}>Diary</Link>
                <br/>
                <Link to={'/deeee'}>New</Link>
            </div>
        );
    }
}

export default RouteTest;